.header {
    font-weight: bold;
    width: 100%;
    height: 100px;
    clip-path: polygon(0 0, 100% 0, 85% 100%, 15% 100%);
    // background-image: url('../../images/ceiling.webp')
    background-size: content;
    background-position: center;
    overflow: visible;
    position: relative;
    z-index: 1; }

.header img {
    position: absolute;
    // top: 30px
    width: 100%;
    height: 100%;
    overflow: visible;
    z-index: 2; }
